.top-right, .top-left, .bottom-left, .bottom-right, .top-center, .bottom-center{
  position: fixed;
  z-index: 1000;
}

.top-right, .top-left, .top-center{
  top: 1rem; 
}

.bottom-right, .bottom-left{
  bottom: 1rem; 
}

.top-center, .bottom-center{
  right: calc(50vw - 7rem);
}
.bottom-right, .top-right{
 right: 1rem; 
}

.bottom-left, .top-left{
  left: 1rem; 
 }

.round {
  border-radius: 50%;
  height: 12px;
  width:12px;
  box-shadow: 1px 1px 2px black;
  display: inline-block;
}

.hoverr:hover {
  cursor: pointer;
  transform: scale(1.1);
  transition: 200ms ease-out;
}

.active-nav{
 color: white;
}

.activee{
  border: 1px solid;
  padding: .1em;

  border-radius: .3em;
}

.full{
  height: 100vh;
  width: 100vw;
}

.eventHover:hover {
 background-color: rgb(112, 214, 221);
 
}
.round-bordered{
  border-radius: 5em;
  border: 2px solid;
}

.pad{
  padding: .35em;
}

.shadow{
  box-shadow: 0 2px 6px rgba(0,0,0,0.25), 0 3px 3px rgba(0,0,0,0.22);
}

.img-shadow{
  filter: drop-shadow(2px 2px 3px gray);
}

.gray{
  background: gray;
}

.white{
  background: rgb(223, 218, 218);
}

.round-btn{
  color: white;
  border-radius: 50%;
  border: 0;
  font-size: 1em;
  width: 2em;
  height: 2em;
  margin: 0.2em;
}

.round-btn:hover{
    background: red;
}
.scale-hover:hover{
  transition: 200ms ease-out;
  transform: scale(1.2);
}

select{
  border-radius: 20px;
}

.right:hover, .left:hover{
  transform: scale(1.5);
}
.right{
  float: right;
}
.left{
  justify-self: start;
}

.hover:hover  +  .hover-show {
  opacity: 1 !important;
}

.hover{
  cursor: pointer;
}
a.underline:hover{
  transition: 200ms ease-out;
  text-decoration: underline;
}
@media (max-height: 600px){
  .hide-height{
    display: none;
  }
}

.wave{
  animation: wave 7s cubic-bezier( 0.36, 0.45, 0.63, 0.53) -.125s infinite, swell 7s ease -1.25s infinite;
}
.pad{
  font-size: 1.112em;
}

.warning{
  color: orange;
}

.error{
  color: tomato;
}

.success{
  color: teal;
