.appear {
  animation: aapearr 300ms ease-out;
}

.slide-up {
  animation: slide-up-animation 400ms linear;
}

.slide-in {
  animation: slide 400ms linear;
}

.spin {
  animation: spins 1s linear infinite;
}

.spin-360 {
  animation: spinss 15s linear infinite;
}

.glow {
  animation: glows 3s linear infinite;
}

.hints-appear {
    transform: scale(0);
  }
  
.hints-appear.hints-appear-active {
    transform: scale(1);
    transition: transform .5s ease-in;
  }

.fade-enter {
    opacity: 0;
    z-index: 1;
  }
  
  .fade-enter.fade-enter-active {
    opacity: 1;
    transition: opacity 250ms ease-in;
  }

@keyframes slide {
  0% {
    transform: translate(60%, 0%);
    opacity: (0);
  }
  100% {
    transform: translate(0, 0);
    opacity: (1);
  }
}
@keyframes slide-up-animation {
  0% {
    transform: translateY(10%);
    opacity: (0);
  }
  100% {
    transform: translate(0, 0);
    opacity: (1);
  }
}


@keyframes apearr {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes glows {
  0% {
    filter: drop-shadow(6px 6px 7px black);
  }
  50% {
    filter: drop-shadow(2px 2px 2px black);
  }
  100% {
    filter: drop-shadow(6px 6px 7px black);
  }
}

@keyframes spins {
  0% {
    transform: rotate(20deg);
  }
  100% {
    transform: rotate(110deg);
  }
}

@keyframes spinss {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(359deg);
  }
}
@media (min-width: 860px) {
  .desktop-margin {
    margin: 0 0 1em 2em;
  }
}

@keyframes wave {
  0% {
    margin-left: 0;
  }
  100% {
    margin-left: -1600px;
  }
}

@keyframes swell {
  0%, 100% {
    transform: translate3d(0,-25px,0);
  }
  50% {
    transform: translate3d(0,5px,0);
  }
}